import { makeStyles } from '@material-ui/core';

export const stylesFooter = {
    footer: {
        boxSizing: 'content-box',
        marginTop: '30px',
        backgroundColor: 'rgba(34, 32, 32, 0.295)',
        fontSize: '1.1rem',
        bottom: 0,
        position: 'fixed',
        width: '100%'
    },
    
    footerContainer: {
        padding: '1em',
        height: '60px',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-evenly',
        alignItems: 'center',
    },

    gitLink: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        color: 'black',
    },
    
    gitLogo: {
        marginRight: '5px',
    },
    
    gitName: {        
        '&:hover': {
            textDecoration: 'underline',
        }
    },
    
    github: {
        marginRight: '2em',
    },
    
    rsLink: {
        width: '100px',
        height: '40px',
    },

    rsLogo: {
        width: '120px',
        height: '50px',

        '& img': {
            width: '100%',
            height: '100%', 
        }
    }
};

const styles = makeStyles(stylesFooter);

export default styles;